header {

  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 15px;

    ul {
      display: flex;
      list-style: none;
      align-items: center;
      padding-left: 0;

      a {
        text-decoration: none;
        color: $main-c;
        font-size: 16px;
        line-height: 19px;
        font-weight: bold;
        outline: none;
        margin-right: 30px;
      }
    }
  }
}

.nav-ul {
  display: flex;
}

.nav-person_img {
  margin-right: 4px;
}

.main-img {
  display: flex;

  img {
    width: 100%;
  }
}

.header-logo {
  width: 30px;
  height: 30px;
  margin-left: 11px;

  a {
    outline: none;
  }

  img {
    width: 100%;
  }
}
